module.exports = {
    getDataUrl: getDataUrl,
    getData: getData
}

function getDataUrl(zenodoId){
    return new Promise(function(resolve, reject){
        let recordId = zenodoId.split('.')[1]; 
        console.log("The zenodo URL is " + zenodoId + " " + recordId);
    
        if (!recordId || !recordId.match(/^[0-9]+$/)){
            resolve();
        }
    
        let url = "https://zenodo.org/api/records/" + recordId
        console.log(url)
        getFromLocation(url).then(function(data){
            console.log("The data from zenodo is ")
            console.log(data.data)
           if (data.data.hasOwnProperty('files') && data.data.files.length > 0 && data.data.files[0].hasOwnProperty('links') && data.data.files[0].links.hasOwnProperty('download')){
                console.log("The second url is ")
                console.log( data.data.files[0].links.download )
                getFromLocation(data.data.files[0].links.download).then(function(data2){
                    console.log("The data2 is ")
                    console.log(data2)
                    resolve(data2)
                })
            }
            else {
                resolve();
            }
        })
    })
}

